.check-box {
  color: var(--text-accent-color);
  width: 93px;
  height: 34px;
  border: 1px solid var(--text-accent-color);
  background-color: #0000;
  border-radius: 4px;
  justify-content: space-between;
  align-items: center;
  padding: 5px 8px;
  display: flex;
}

.check-box:focus, .check-box:hover {
  background-color: var(--text-accent-color);
  color: var(--text-primary-inverse-color);
}

@media screen and (min-width: 768px) {
  .to-choose-tablet {
    width: 324px;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .to-choose-tablet {
    width: 523px;
    gap: 0;
  }
}

@media screen and (max-width: 767px) {
  .to-choose-tablet {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .letter-tablet {
    width: 24px;
    height: 25px;
    text-align: center;
    color: var(--text-secondary-color);
    font-size: 14px;
    font-weight: 700;
    line-height: 21px;
    display: inline-block;
  }
}

@media screen and (min-width: 1280px) {
  .letter-tablet {
    width: 40px;
    height: 40px;
    font-size: 24px;
    line-height: 36px;
  }
}

@media screen and (min-width: 768px) {
  .mobile {
    display: none;
  }
}

.letter {
  height: 25px;
  color: #5f6775;
  align-items: center;
  padding: 0 10px;
  font-size: 10px;
  font-weight: 700;
  line-height: 15px;
  display: flex;
}

.letter:hover {
  color: #fff;
  background-color: var(--text-accent-color);
}

.check-box-svg {
  fill: var(--text-accent-color);
}

.check-box-svg:hover, .check-box-svg:focus {
  fill: var(--text-primary-inverse-color);
}

.js-letter {
  all: unset;
}

.check-box-svg {
  transition: transform .3s ease-in-out;
}

.check-box--open .check-box-svg {
  transform: rotate(180deg);
}

.to-choose {
  z-index: 1;
  border: 1px solid var(--text-accent-color);
  max-height: 84px;
  width: 93px;
  background-color: #fff;
  border-radius: 4px;
  padding: 8px 0;
  display: none;
  position: absolute;
  overflow-y: auto;
}

.to-choose--visible {
  display: block;
}

.js-letter {
  width: 100%;
  text-align: left;
  cursor: pointer;
  background-color: #0000;
  border: none;
  padding: 8px 0;
  display: block;
}

.js-letter:hover {
  color: var(--background-accent-color);
  background-color: #0000;
}

.js-letter:focus {
  color: var(--text-accent-color);
  background-color: #0000;
  outline: none;
}

